import { NavigationChevronDown } from '@repay/cactus-icons'
import { BorderSize, ColorStyle, Shape } from '@repay/cactus-theme'
import PropTypes from 'prop-types'
import React, { createContext, ReactElement, useContext, useEffect, useState } from 'react'
import styled, {
  css,
  DefaultTheme,
  FlattenSimpleInterpolation,
  StyledComponent,
} from 'styled-components'
import { margin, MarginProps } from 'styled-system'

import { keyPressAsClick } from '../helpers/a11y'
import { border, fontSize } from '../helpers/theme'
import useId from '../helpers/useId'
import Pagination from '../Pagination/Pagination'
import PrevNext from '../PrevNext/PrevNext'
import Table from '../Table/Table'

interface DataGridContextType {
  addDataColumn: (dataColumn: DataColumn) => void
  addColumn: (key: string, columnFn: ColumnFn, title?: string) => void
}

type ColumnFn = (rowData: { [key: string]: any }) => React.ReactNode

interface DataColumn {
  id: string
  title: string
  sortable: boolean
  asComponent?: React.ComponentType<any>
}

const DataGridContext = createContext<DataGridContextType>({
  addDataColumn: (): void => {
    return
  },
  addColumn: (): void => {
    return
  },
})

interface DataGridProps extends MarginProps {
  data: { [key: string]: any }[]
  paginationOptions?: PaginationOptions
  sortOptions?: SortOption[]
  onPageChange: (newPageOptions: PaginationOptions) => void
  onSort: (newSortOptions: SortOption[]) => void
  children: React.ReactNode
  fullWidth?: boolean
  className?: string
  resultsCountText?: string
  pageSizeSelectLabel?: string
  makePageSizeLabel?: (pageSize: number) => string
  paginationProps?: {
    label?: string
    currentPageLabel?: string
    prevPageLabel?: string
    nextPageLabel?: string
    lastPageLabel?: string
    makeLinkLabel?: (page: number) => string
  }
  prevNextProps?: {
    prevText?: string
    nextText?: string
    disableNext?: boolean
  }
}

interface PaginationOptions {
  currentPage: number
  pageSize: number
  pageCount?: number
  pageSizeOptions?: number[]
}

interface SortOption {
  id: string
  sortAscending: boolean
}

interface ResultsViewSectionProps {
  resultsCountText?: string
  paginationOptions?: PaginationOptions
  onPageChange: (newPageOptions: PaginationOptions) => void
  makePageSizeLabel?: (pageSize: number) => string
  pageSizeSelectLabel?: string
  className?: string
}

interface PageSizeSelectProps {
  paginationOptions: PaginationOptions
  onPageChange: (newPageOptions: PaginationOptions) => void
  makePageSizeLabel?: (pageSize: number) => string
  pageSizeSelectLabel?: string
  className?: string
}

interface DataColumnProps {
  id: string
  title: string
  sortable?: boolean
  as?: React.ComponentType<any>
}

interface ColumnProps {
  children: ColumnFn
  title?: string
}

interface DataColumnObject {
  title: string
  sortable: boolean
  asComponent?: React.ComponentType<any>
}

interface ColumnObject {
  columnFn: ColumnFn
  title?: string
}

const DataGridBase = (props: DataGridProps): ReactElement => {
  const {
    children,
    data,
    fullWidth,
    sortOptions,
    onSort,
    paginationOptions,
    onPageChange,
    className,
    resultsCountText,
    pageSizeSelectLabel,
    makePageSizeLabel,
    paginationProps,
    prevNextProps,
  } = props
  const [columns, setColumns] = useState(new Map<string, DataColumnObject | ColumnObject>())

  const addDataColumn = ({ id, title, sortable, asComponent }: DataColumn): void => {
    setColumns(new Map(columns.set(id, { title, sortable, asComponent })))
  }

  const addColumn = (key: string, columnFn: ColumnFn, title?: string): void => {
    setColumns(new Map(columns.set(key, { columnFn, title })))
  }

  return (
    <div className={className}>
      <DataGridContext.Provider value={{ addDataColumn, addColumn }}>
        {children}
        <ResultsViewSection
          onPageChange={onPageChange}
          pageSizeSelectLabel={pageSizeSelectLabel}
          resultsCountText={resultsCountText}
          paginationOptions={paginationOptions}
          makePageSizeLabel={makePageSizeLabel}
        />
        <Table fullWidth={fullWidth}>
          <Table.Header>
            {[...columns.keys()].map(
              (key): ReactElement => {
                let column = columns.get(key)
                if (column && column.hasOwnProperty('sortable')) {
                  column = column as DataColumnObject
                  let sortOpt: SortOption | undefined = undefined
                  if (column.sortable && sortOptions !== undefined) {
                    sortOpt = sortOptions.find((opt: SortOption): boolean => opt.id === key)
                  }
                  const flipChevron = sortOpt !== undefined && sortOpt.sortAscending === true
                  return (
                    <Table.Cell
                      className={`table-cell ${flipChevron ? 'flip-chevron' : ''}`}
                      key={key}
                      aria-sort={
                        column.sortable
                          ? sortOpt
                            ? sortOpt.sortAscending
                              ? 'ascending'
                              : 'descending'
                            : 'none'
                          : undefined
                      }
                    >
                      {column.sortable && sortOptions !== undefined ? (
                        <HeaderButton
                          onClick={(): void => {
                            const { sortAscending: currentSortAscending } = sortOptions[0] || {}
                            const newOptions = [
                              { id: key, sortAscending: sortOpt ? !currentSortAscending : false },
                            ]
                            onSort(newOptions)
                          }}
                        >
                          {column.title}

                          {sortOpt !== undefined && <NavigationChevronDown aria-hidden="true" />}
                        </HeaderButton>
                      ) : (
                        column.title
                      )}
                    </Table.Cell>
                  )
                } else {
                  column = column as ColumnObject
                  return <Table.Cell key={`col-${key}`}>{column.title || ''}</Table.Cell>
                }
              }
            )}
          </Table.Header>
          <Table.Body>
            {data.map(
              (datum, datumIndex): ReactElement => (
                <Table.Row key={`row-${datumIndex}`}>
                  {[...columns.keys()].map(
                    (key, keyIndex): ReactElement => {
                      let column = columns.get(key)
                      if (column && column.hasOwnProperty('sortable')) {
                        column = column as DataColumnObject
                        const AsComponent = column.asComponent
                        return (
                          <Table.Cell key={`cell-${datumIndex}-${keyIndex}`}>
                            {AsComponent ? <AsComponent value={datum[key]} /> : datum[key]}
                          </Table.Cell>
                        )
                      } else {
                        column = column as ColumnObject
                        return (
                          <Table.Cell key={`cell-${datumIndex}-${keyIndex}`}>
                            {column && column.columnFn(datum)}
                          </Table.Cell>
                        )
                      }
                    }
                  )}
                </Table.Row>
              )
            )}
          </Table.Body>
        </Table>
      </DataGridContext.Provider>
      <div className="pagination">
        {paginationOptions !== undefined ? (
          paginationOptions.pageCount ? (
            <Pagination
              currentPage={paginationOptions.currentPage}
              pageCount={paginationOptions.pageCount}
              onPageChange={(page: number): void => {
                onPageChange({ ...paginationOptions, currentPage: page })
              }}
              {...paginationProps}
            />
          ) : (
            <PrevNext
              disablePrev={paginationOptions.currentPage === 1}
              onNavigate={(direction: 'prev' | 'next'): void => {
                onPageChange({
                  ...paginationOptions,
                  currentPage:
                    direction === 'prev'
                      ? paginationOptions.currentPage - 1
                      : paginationOptions.currentPage + 1,
                })
              }}
              disableNext={
                prevNextProps
                  ? prevNextProps.disableNext || data.length < paginationOptions.pageSize
                  : data.length < paginationOptions.pageSize
              }
              {...prevNextProps}
            />
          )
        ) : null}
      </div>
    </div>
  )
}

export const DataGrid = styled(DataGridBase)`
  display: inline;
  flex-direction: column;
  width: ${(p): string => (p.fullWidth ? '100%' : 'auto')};
  overflow-x: auto;
  ${margin}

  ${(p): string | undefined => p.theme.mediaQueries && p.theme.mediaQueries.medium} {
    display: inline-flex;
  }

  .results-count {
    margin-bottom: 40px;
    margin-left: 16px;
    margin-right: auto;
  }

  th {
    ${NavigationChevronDown} {
      margin-left: 8px;
    }
  }

  .flip-chevron {
    ${NavigationChevronDown} {
      transform: rotate3d(1, 0, 0, 180deg);
    }
  }

  .pagination {
    display: flex;
    justify-content: center;
    margin-right: 16px;
    margin-top: 40px;

    ${(p): string | undefined => p.theme.mediaQueries && p.theme.mediaQueries.medium} {
      justify-content: flex-end;
    }
  }
` as any

const borderMap = {
  thin: css`
    border: 1px solid;
  `,
  thick: css`
    border: 2px solid;
  `,
}

const shapeMap = {
  square: css`
    border-radius: 1px;
  `,
  intermediate: css`
    border-radius: 8px;
  `,
  round: css`
    border-radius: 20px;
  `,
}
const getShape = (shape: Shape): FlattenSimpleInterpolation => shapeMap[shape]

const getBorder = (size: BorderSize): FlattenSimpleInterpolation => borderMap[size]

const HeaderButton = styled.button`
  background: none;
  border: none;
  color: inherit;
  font-size: inherit;
  text-transform: inherit;
  font-weight: inherit;
  padding: 0;
  cursor: pointer;
  position: relative;
  overflow: visible;

  &:focus {
    outline: none;

    &:after {
      content: '';
      box-sizing: border-box;
      position: absolute;
      height: calc(100% + 8px);
      width: calc(100% + 16px);
      bottom: -4px;
      right: -8px;
      border-color: ${(p): string => p.theme.colors.lightContrast};
      ${(p): FlattenSimpleInterpolation => getShape(p.theme.shape)}
      ${(p): FlattenSimpleInterpolation => getBorder(p.theme.border)}
    }
  }
`

const ResultsViewSectionBase = (props: ResultsViewSectionProps): ReactElement | null => {
  const {
    onPageChange,
    resultsCountText,
    pageSizeSelectLabel,
    makePageSizeLabel,
    paginationOptions,
    className,
  } = props
  if (
    !resultsCountText &&
    (!paginationOptions || (paginationOptions && !paginationOptions.pageSizeOptions))
  ) {
    return null
  } else {
    return (
      <div className={className}>
        {resultsCountText && <span className="results-count-text">{resultsCountText}</span>}
        {paginationOptions !== undefined && paginationOptions.pageSizeOptions && (
          <PageSizeSelect
            paginationOptions={paginationOptions}
            pageSizeSelectLabel={pageSizeSelectLabel}
            makePageSizeLabel={makePageSizeLabel}
            onPageChange={onPageChange}
          />
        )}
      </div>
    )
  }
}

const ResultsViewSection = styled(ResultsViewSectionBase)`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 16px;
  margin-bottom: 40px;

  .results-count-text {
    margin-bottom: 8px;
  }

  ${(p): string | undefined => p.theme.mediaQueries && p.theme.mediaQueries.medium} {
    flex-direction: row;
    align-items: flex-start;

    .results-count-text: {
      margin-bottom: 0;
    }
  }
`

const PageSizeSelectBase = (props: PageSizeSelectProps): ReactElement => {
  const { paginationOptions, onPageChange, pageSizeSelectLabel, className } = props
  const { pageSizeOptions } = paginationOptions
  const makePageSizeLabel = props.makePageSizeLabel as (pageSize: number) => string
  return (
    <div className={className}>
      <span>{pageSizeSelectLabel || 'View'}</span>
      <ol className="page-options-list">
        {pageSizeOptions &&
          pageSizeOptions.map(
            (pageSize): ReactElement => {
              const isCurrentPageSize = paginationOptions.pageSize === pageSize
              return (
                <li className="page-option" key={`page-size-option-${pageSize}`}>
                  <a
                    role="link"
                    aria-selected={isCurrentPageSize ? 'true' : 'false'}
                    onClick={(): void => {
                      onPageChange({ ...paginationOptions, pageSize: pageSize })
                    }}
                    onKeyPress={keyPressAsClick}
                    tabIndex={isCurrentPageSize ? undefined : 0}
                    aria-label={makePageSizeLabel(pageSize)}
                  >
                    {pageSize}
                  </a>
                </li>
              )
            }
          )}
      </ol>
    </div>
  )
}

const PageSizeSelect = styled(PageSizeSelectBase)`
  display: inline-box;

  ${(p): string | undefined => p.theme.mediaQueries && p.theme.mediaQueries.medium} {
    margin-left: auto;
  }

  span {
    margin-right: 8px;
  }

  .page-options-list {
    display: inline-flex;
    flex-wrap: nowrap;
    list-style: none;
    margin: 0;
    padding: 0;
  }

  .page-option {
    min-width: 15px;
    height: 24px;
    text-align: center;
    background: none;
    appearance: none;
    padding: 2px 8px;
    display: block;

    border-left: ${(p): string => border(p.theme, 'lightContrast')};

    &:last-child {
      border-right: ${(p): string => border(p.theme, 'lightContrast')};
    }

    &,
    a {
      color: ${(p): string => p.theme.colors.darkestContrast};
      ${(p): string => fontSize(p.theme, 'small')};
      line-height: 18px;
      text-decoration: none;
    }

    a {
      cursor: pointer;
      padding: 3px;
      vertical-align: middle;
      line-height: 18px;
      display: block;
      border-radius: 8px;

      text-decoration: none;
      color: inherit;

      &:hover {
        color: ${(p): string => p.theme.colors.callToAction};
      }

      &:active,
      &:focus {
        // Re-stated to prevent a small shift when the button is clicked in Firefox
        padding: 3px;
        outline: none;
        ${(p): ColorStyle => p.theme.colorStyles.callToAction};
      }

      &[aria-selected='true'] {
        ${(p): ColorStyle => p.theme.colorStyles.base};
      }
    }
  }
`

export const DataColumn = (props: DataColumnProps): ReactElement => {
  const { id, title, sortable = false, as: asComponent } = props
  const { addDataColumn } = useContext(DataGridContext)
  useEffect((): void => {
    addDataColumn({ id, title, sortable, asComponent })
  }, [asComponent, id, sortable, title]) // eslint-disable-line react-hooks/exhaustive-deps
  return <React.Fragment />
}

const Column = (props: ColumnProps): ReactElement => {
  const { children, title } = props
  const { addColumn } = useContext(DataGridContext)
  const fnKey = useId()
  useEffect((): void => {
    if (children && typeof children === 'function') {
      addColumn(fnKey, children, title)
    }
  }, [children]) // eslint-disable-line react-hooks/exhaustive-deps
  return <React.Fragment />
}

PageSizeSelect.defaultProps = {
  makePageSizeLabel: (pageSize: number): string => `View ${pageSize} rows per page`,
}

DataGrid.propTypes = {
  data: PropTypes.arrayOf(PropTypes.object).isRequired,
  paginationOptions: PropTypes.shape({
    currentPage: PropTypes.number.isRequired,
    pageSize: PropTypes.number.isRequired,
    pageCount: PropTypes.number,
    pageSizeOptions: PropTypes.arrayOf(PropTypes.number),
  }),
  sortOptions: PropTypes.arrayOf(
    PropTypes.shape({ id: PropTypes.string.isRequired, sortAscending: PropTypes.bool.isRequired })
  ),
  onPageChange: PropTypes.func.isRequired,
  onSort: PropTypes.func.isRequired,
  children: PropTypes.node.isRequired,
  fullWidth: PropTypes.bool,
  resultsCountText: PropTypes.string,
  pageSizeSelectLabel: PropTypes.string,
  makePageSizeLabel: PropTypes.func,
  paginationProps: PropTypes.shape({
    label: PropTypes.string,
    currentPageLabel: PropTypes.string,
    prevPageLabel: PropTypes.string,
    nextPageLabel: PropTypes.string,
    lastPageLabel: PropTypes.string,
    makeLinkLabel: PropTypes.func,
  }),
  prevNextProps: PropTypes.shape({
    prevText: PropTypes.string,
    nextText: PropTypes.string,
    disableNext: PropTypes.bool,
  }),
}

DataColumn.propTypes = {
  id: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  sortable: PropTypes.bool,
  as: PropTypes.elementType,
}

Column.propTypes = {
  children: PropTypes.func.isRequired,
  title: PropTypes.string,
}

type DataGridType = StyledComponent<typeof DataGridBase, DefaultTheme, DataGridProps> & {
  DataColumn: React.ComponentType<DataColumnProps>
  Column: React.ComponentType<ColumnProps>
}

DataGrid.DataColumn = DataColumn
DataGrid.Column = Column

export default DataGrid as DataGridType
